@import "./colors.scss";

.bandeau {
  display: flex;
  margin: 0.5vw 4vw;
}
.img-base {
  width: 100%;
  height: 15vw;
  border-radius: 20px;
  position: relative;
}
.img-acceuil {
  filter: brightness(50%);
  object-fit: cover;
  box-shadow: 0 5px 0 0 rgba(0, 0, 0, 0.1);
}
.img-about {
  filter: brightness(90%);
  object-fit: cover;
}
.img-base h1 {
  color: $color-1;
  position: absolute;
  left: 20vw;
  top: 3.3vw;
  font-size: 3.3vw;
}

@media screen and (max-width: 600px) {
  .bandeau {
    height: 25vw;
  }
  .img-base {
    height: 100%;
    border-radius: 10px;
  }
  .img-base h1 {
    width: 58%;
    font-size: 5.6vw;
    left: 5vw;
    top: 1.5vw;
  }
}
