@import "./colors.scss";

.erreur-img {
  display: flex;
  justify-content: center;
  width: 40vw;
  margin: 5vw auto 5vw auto;
}
p {
  display: flex;
  justify-content: center;
  color: $color-2;
  font-size: 2.5vw;
  font-weight: 600;
  margin-bottom: 5vw;
}
.back {
  display: flex;
  justify-content: center;
  color: $color-4;
  cursor: pointer;
  margin-bottom: 5vw;
  font-weight: 600;
  font-size: 1.5vw;
}

@media screen and (max-width: 767px) {
  .erreur-img {
    width: 55vw;
    margin: 10vw auto 10vw auto;
  }
  p {
    font-size: 3.7vw;
    margin: 10vw 17vw;
    text-align: center;
  }
  .back {
    font-size: 3vw;
  }
}
